/**
 *
 */
export class DrfRefDiffPanel extends DiffPanelBase<IItemUiEnv> {

	action: DrfRefDiffAction;

	erased?: boolean

	initPanel(action: OtherItemDiffAction, bar: IWedDiffBar): this {
		this._initPanel(action, bar);
		this.shadowRoot.appendChild(this.result);
		this.execDiff();
		return this;
	}

	setDiff(params: JDrfRefWedDiffBarParams | undefined | null): void {
		this.erased = params === null;
		this.execDiff();
	}

	buildDiffParams(): JDrfRefWedDiffBarParams {
		return {diffId: this.action.getId()}
	}

	execDiff(fullRefresh?: boolean): Promise<void> {
		if (fullRefresh) this.erased = false;
		return callDiff(this.erased ? "" : "special:drf:ref", this, EXmlDiffMode.diff);
	}
}

