/**
 *
 */
export class HistoItemDiffPanel extends DiffPanelBase<IItemUiEnv> {

	reg: IReg<IItemUiEnv>;
	action: HistoItemDiffAction;
	protected fields: string;

	/** Source de diff à afficher.*/
	srcDiff?: srcRef;

	/** Point de la ligne d'histo correspondant à l'item courant. */
	currentPoint: HistoPoint;

	/** Point de la ligne d'histo actuellement comparée */
	diffPoint: HistoPoint | null;


	scrollLine: ArrowScrollBox;
	topLine: HTMLElement;
	middleLine: HTMLElement;
	bottomLine: HTMLElement;
	diffSelView: HTMLElement;

	protected userColors: Map<account, string> = new Map();
	protected colorOffset = 0;

	protected _tt: PopupTooltip;

	initPanel(action: HistoItemDiffAction, bar: IWedDiffBar): this {
		this._initPanel(action, bar);
		this.topLine = <div id="top"/>;
		this.middleLine = <div id="middle"/>;
		this.bottomLine = <div id="bottom"/>;
		this.diffSelView = <div id="sel"/>;
		this.scrollLine = <ArrowScrollBox>
			<div id="lines">
				{this.topLine}
				{this.middleLine}
				{this.bottomLine}
			</div>
		</ArrowScrollBox> as ArrowScrollBox;

		this.shadowRoot.append(
			this.scrollLine,
			<div id="states">
				{this.diffSelView}
				{this.result}
			</div>
		);
		this.fields = this.reg.env.wsp.getShortDescFields().concat("metaFlag", "metaComment").join('*');

		this.middleLine.addEventListener("keydown", this.onKeyDownInLine);
		this.middleLine.addEventListener("dblclick", this.onDblclickInLine);

		this.getColor(this.reg.env.securityCtx.account);//réserve la 1ère couleur pour le user courant.
		return this;
	}

	connectDoc(): void {
		super.connectDoc();
		this.updateHisto();
	}

	unconnectDoc(): void {
		super.unconnectDoc();
		this.resetHisto();
	}

	onViewShown() {
		//sur onViewShown / onViewHidden pour désabonnment des diff affichée dans les dialogs d'édition des metas du txt
		if (!this._onItemChange && this.reg.env.longDescChange) this.reg.env.longDescChange.add((this._onItemChange = this.onItemChange.bind(this)));
	}

	onViewHidden(closed?: boolean): void {
		if (closed && this.reg.env.longDescChange) this.reg.env.longDescChange.delete(this._onItemChange);
		this.closeTooltip();
	}

	onItemChange() {
		if (this.docConnected) this.updateHisto();
	}

	protected _onItemChange: () => void;

	async updateHisto(): Promise<void> {
		const hadFocus = this.matches(":focus-within");
		this.resetHisto();
		try {
			//const ld = this.reg.env.longDesc;
			const editedSrc = (this.wedMgr.docHolder.house as IWspXmlHouse).srcFields;
			const hist = await WSP.fetchHistory(this.reg.env.wsp, this, editedSrc.srcLiveUri || editedSrc.srcUri, this.fields);
			//console.log("::hist:::::", hist);

			const count = hist.histNodes.length;

			if (count <= 1) {
				this.middleLine.append("Aucune entrée d'historique disponible");
				//Entrée d'historique disparue, on efface
				this.srcDiff = null;
				this.execDiff();
				return;
			}

			const rect = this.topLine.parentElement.getBoundingClientRect();
			const h = Math.floor(rect.height / 3);
			const hHalf = h / 2;
			const w = Math.round(GFX.bound(h + 4, (rect.width - count) / (count + 1), h * 3));
			const wHalf = w / 2;
			const linePath = `M 0,${hHalf} H ${w}`;
			const rate = ((h - 2) / 14).toString();

			let nextOnTop = false;
			let nextWidthTop = 0;
			let nextWidthBottom = 0;

			//Recherche du current, 1er si pas trouvé
			const currentOffset = hist.histNodes.findIndex((src) => src.srcUri === editedSrc.srcUri);
			const currentDate = new Date(currentOffset >= 0 ? hist.histNodes[currentOffset].srcDt : Date.now());

			//Ajout flèche
			this.topLine.appendChild(<div/>);
			nextWidthTop += w;
			this.middleLine.appendChild(JSX.asSvg(() => <svg id="frontArrowEnd" width={w} height={h} fill="none" stroke-width="2" stroke="var(--alt2-color)">
				<path d={`M 0,${hHalf} H ${hHalf}`}/>
				<path d={`M 1,2 ${hHalf},${hHalf} 1,${h - 2}`}/>
			</svg>));
			this.bottomLine.appendChild(<div/>);
			nextWidthBottom += w;

			let srcDiffFound = false;
			let previousTime: string = "";
			for (let i = 0; i < hist.histNodes.length; i++) {
				const src = hist.histNodes[i];
				//Ajout de chaque noeud
				let srcTime: string;
				if (i === currentOffset) {
					if (nextWidthTop > 0) (this.topLine.lastElementChild as HTMLElement).style.width = nextWidthTop + "px";
					this.topLine.append(JSX.asSvg(() => <svg width={w} height={h}>
							<path transform={`translate(${wHalf - hHalf})`} d={`M 0,3 H ${h} L ${hHalf},${h}`} fill="var(--alt2-color)"/>
						</svg>),
						<div/>);
					nextWidthTop = 0;
					if (nextWidthBottom > 0) (this.bottomLine.lastElementChild as HTMLElement).style.width = nextWidthBottom + "px";
					this.bottomLine.append(JSX.asSvg(() => <svg width={w} height={h}>
							<path transform={`translate(${wHalf - hHalf})`} d={`M 0,${h - 3} H ${h} L ${hHalf},0`} fill="var(--alt2-color)"/>
						</svg>),
						<div/>);
					nextWidthBottom = 0;
					nextOnTop = !nextOnTop;
				} else {
					//Time
					srcTime = LOCALE.formatMinDuration(currentDate, new Date(src.srcDt), true, '+-');
					if (srcTime === previousTime) {
						//Pas de marque de temps
						srcTime = "";
						nextWidthTop += w;
						nextWidthBottom += w;
					} else {
						//Nouvelle marque de temps
						if (nextOnTop) {
							if (nextWidthTop > 0) (this.topLine.lastElementChild as HTMLElement).style.width = nextWidthTop + "px";
							this.topLine.appendChild(<wed-histo-time class={i > currentOffset ? "timeAfter" : "timeBefore"}>{srcTime}</wed-histo-time>);
							nextWidthTop = w;
							nextWidthBottom += w;
						} else {
							if (nextWidthBottom > 0) (this.bottomLine.lastElementChild as HTMLElement).style.width = nextWidthBottom + "px";
							this.bottomLine.appendChild(<wed-histo-time class={i > currentOffset ? "timeAfter" : "timeBefore"}>{srcTime}</wed-histo-time>);
							nextWidthBottom = w;
							nextWidthTop += w;
						}
						previousTime = srcTime;
						nextOnTop = !nextOnTop;
					}
				}

				//Middle
				const color = this.getColor(src.srcUser);
				const shape = JSX.asSvg(() => {
					if (ITEM.isHistoryUri(src.srcUri)) {
						src.srcLiveUri = hist.liveNode.srcUri; //enrichissement des entrées d'historique
						if (src.metaFlag > 0) {
							return <path transform={`matrix(${rate},0,0,${rate},${wHalf - hHalf},0)`} d="m 18.1,8.2 -5.25,2.25 0.7,5.3 L 9,12.35 4.76,15.6 5.43,9.93 0.5,7.9 5.74,5.65 5,0.35 9.6,3.77 13.83,0.5 13.2,6.18 Z" stroke-width="2" fill={color}
													 stroke="var(--alt2-color)"/>;
						} else {
							return <circle cx={wHalf} cy={hHalf} r={hHalf - 2} stroke-width="2" fill={color} stroke="var(--alt2-color)"/>;
						}
					} else if (src.metaFlag > 0) {
						return <g>
							<rect x={wHalf - hHalf + 1} y="0" width={h} height={h} fill="var(--alt2-color)"/>
							<path transform={`matrix(${rate},0,0,${rate},${wHalf - hHalf},0)`} d="m 18.1,8.2 -5.25,2.25 0.7,5.3 L 9,12.35 4.76,15.6 5.43,9.93 0.5,7.9 5.74,5.65 5,0.35 9.6,3.77 13.83,0.5 13.2,6.18 Z" fill={color}/>
						</g>
					} else {
						return <rect x={wHalf - hHalf + 1} y="1" width={h - 2} height={h - 2} stroke-width="2" fill={color} stroke="var(--alt2-color)"/>
					}
				});
				const point = <wed-histo-point>{JSX.asSvg(() => <svg width={srcTime ? w - 1 : w} height={h}>
					<path d={linePath} stroke-width="2" stroke="var(--alt2-color)"/>
					{shape}
					{src.metaComment ? <path transform={`matrix(${rate},0,0,${rate},${wHalf},0)`} d="M 1,5.7 5.8,0 8,1.9 3,7.4 0,9" fill="var(--alt1-color)"/> : undefined}
				</svg>)}</wed-histo-point> as HistoPoint;
				if (srcTime) point.classList.add(i > currentOffset ? "timeAfter" : "timeBefore");
				point.shortDesc = src;

				this.middleLine.appendChild(point);

				if (this.srcDiff && !srcDiffFound && SRC.srcRef(src) === this.srcDiff) {
					this._setDiffPoint(point);
					srcDiffFound = true;
				}
				if (i === currentOffset) this.currentPoint = point;
			}
			if (!srcDiffFound && this.srcDiff) {
				//Entrée d'historique disparue, on efface
				this.srcDiff = null;
				this.execDiff();
			}

			if (nextWidthTop > 0) (this.topLine.lastElementChild as HTMLElement).style.width = nextWidthTop + "px";
			if (nextWidthBottom > 0) (this.bottomLine.lastElementChild as HTMLElement).style.width = nextWidthBottom + "px";

			if (this.currentPoint) {
				this.currentPoint.focusable = true;
				this.scrollLine.scrollToChild(this.currentPoint);
				if (hadFocus) this.currentPoint.focus();
			}
		} catch (e) {
			ERROR.log(e);
		}
	}

	resetHisto() {
		this.topLine.textContent = null;
		this.middleLine.textContent = null;
		this.bottomLine.textContent = null;
	}

	onKeyDownInLine(this: HTMLElement, ev: KeyboardEvent) {
		if (ACTION.isAnyControlPressed(ev)) return;
		const histoPoint = ev.target as HistoPoint;
		switch (ev.key) {
		case "ArrowLeft":
			if (histoPoint.nextElementSibling instanceof HistoPoint) histoPoint.nextElementSibling.focus();
			break;
		case "ArrowRight":
			if (histoPoint.previousElementSibling instanceof HistoPoint) histoPoint.previousElementSibling.focus();
			break;
		case "Enter":
			histoPoint.click();
			break;
		default:
			return;
		}
		ev.preventDefault();
		ev.stopPropagation();
	}

	onDblclickInLine(this: HTMLElement, ev: MouseEvent) {
		const histoPoint = DOM.findParent(ev.target as Node, this, (n): n is HistoPoint => n instanceof HistoPoint);
		if (histoPoint) ShowHistoDiff.SINGLETON.executeIfAvailable(histoPoint, ev);
	}

	setCurrentTooltip(tt: PopupTooltip) {
		this._tt?.close();
		this._tt = tt;
	}

	closeTooltip(): void {
		if (this._tt) {
			this._tt.close();
			this._tt = null;
		}
	}

	async execDiff(fullRefresh?: boolean): Promise<void> {
		if (fullRefresh) await this.updateHisto();
		return callDiff(this.srcDiff, this, EXmlDiffMode.diff);
	}

	setDiff(params: JHistoItemWedDiffBarParams | null): void {
		this.setSrcDiff(params?.srcDiff);
	}

	setSrcDiff(srcDiff: srcRef): void {
		this.srcDiff = srcDiff;
		this.execDiff();
		let point: HistoPoint;
		if (this.middleLine.hasChildNodes()) {
			//line calculée
			for (let ch of this.middleLine.children) {
				if (ch instanceof HistoPoint && SRC.srcRef(ch.shortDesc) === srcDiff) {
					point = ch;
					break;
				}
			}
		}
		this._setDiffPoint(point);
	}

	setDiffPoint(histoPoint: HistoPoint): void {
		this._setDiffPoint(histoPoint);
		this.srcDiff = histoPoint ? SRC.srcRef(histoPoint.shortDesc) : null;
		this.execDiff();
	}

	protected _setDiffPoint(histoPoint: HistoPoint): void {
		this.diffPoint?.classList.remove("diff");
		if (histoPoint) {
			histoPoint.classList.add("diff");
			this.diffPoint = histoPoint;
			const dt = new Date(histoPoint.shortDesc.srcDt);
			const date = dt.toLocaleDateString("fr");
			const time = dt.toLocaleTimeString("fr");
			this.diffSelView.textContent = ITEM.isHistoryUri(histoPoint.shortDesc.srcUri) ?
				`Entrée du ${date} à ${time}` :
				"Item courant (${date} à ${time})";
		} else {
			this.diffSelView.textContent = null;
		}
	}

	buildDiffParams(): JHistoItemWedDiffBarParams {
		return {diffId: this.action.getId(), srcDiff: this.srcDiff}
	}

	protected getColor(a: account) {
		if (!a) return "white";
		let color = this.userColors.get(a);
		if (color) return color;
		color = COLORS[this.colorOffset++];
		this.userColors.set(a, color);
		if (this.colorOffset >= COLORS.length) this.colorOffset = 1; // pas 0, on saute la couleur du user courant.
		return color;
	}
}

const COLORS = ["#AAA", "#9370db", "#b22222", "#2e8b57", "#ff4500", "#3c64e6", "#ffe100", "#c99b61", "#b0e0e6", "#ff6293", "#9acd32", "#ffb6c1", "#20b2aa", "#0000c8", "#ffa500", "#789bf2", "#eedfb8", "#4d453a"];

