export class HistoPoint extends HTMLElement implements ITooltipPointer {
	shortDesc: JSrcFields;
	popupTooltip?: PopupTooltip;
	startBtn: ButtonToggle;
	commentInput: HTMLInputElement;

	constructor() {
		super();
		this.onclick = POPUP.promiseTooltip(this, this.onBuildTooltipPoint, {hoverAllowed: true});
		this.onblur = (ev: FocusEvent) => {
			if (this.popupTooltip && !DOM.isAncestor(this.popupTooltip, ev.relatedTarget as HTMLElement)) this.popupTooltip.close();
		}
	}

	get histoPanel(): HistoItemDiffPanel {return DOMSH.findHost<HistoItemDiffPanel>(this)}

	get focusable() {return this.tabIndex === 0}

	set focusable(f: boolean) {
		if (f) {
			for (let ch of this.parentElement.children) {
				if (ch !== this && ch.hasAttribute("tabindex")) ch.removeAttribute("tabindex");
			}
			this.tabIndex = 0;
		} else if (this.hasAttribute("tabindex")) {
			this.removeAttribute("tabindex");
		}
	}

	focus(options?: FocusOptions): void {
		this.focusable = true;
		super.focus(options);
		const scroll = DOM.findParent(this, null, (n): n is ArrowScrollBox => n instanceof ArrowScrollBox);
		scroll?.scrollToChild(this);
	}

	onBuildTooltipPoint(this: void, point: HistoPoint, tooltip: PopupTooltip): IView | null {
		//if (ITEM.isHistoryUri(point.shortDesc.srcUri)) {
		//point.focus();
		const panel = point.histoPanel;
		//console.log(":::onBuildTooltipPoint");
		const dt = new Date(point.shortDesc.srcDt);
		const date = dt.toLocaleDateString("fr");
		const time = dt.toLocaleTimeString("fr");
		const reg = panel.reg;
		const actionsBar = <BarActions î={{reg, actions: reg.getList("actions:item:histoEntry"), actionContext: point, disableFullOverlay: true} as OBarActionsInit<HistoPoint>}/>;
		tooltip.addEventListener("c-show", () => {
			point.classList.toggle("hover", true);
			panel.setCurrentTooltip(tooltip);
			Promise.resolve().then(() => {
				const focusbale = DOMSH.findFlatNext(actionsBar, actionsBar, DOM.IS_focusable);
				if (focusbale) focusbale.focus();
			});
		});
		tooltip.addEventListener("c-beforeclose", () => {
			point.classList.toggle("hover", false);
			panel.setCurrentTooltip(null);
		});
		point.startBtn = <ButtonToggle id="metaStar" î={{reg} as OButtonToggleInit} title="Lorsque l'entrée est marquée, elle est préservée des purges progressives automatiques">
			{JSX.asSvg(() => <svg width="16" height="16">
				<path id="star" stroke-width="1" d="M 11.35,13.56 8,11.1 4.6,13.56 5,9.4 1.2,7.73 5,6 4.56,1.84 7.95,4.33 11.35,1.84 10.9,6 14.75,7.73 10.9,9.43 Z"/>
			</svg>)}
		</ButtonToggle> as ButtonToggle;
		point.startBtn.toggleOn = point.shortDesc.metaFlag > 0;
		point.commentInput = <input id="metaComment" value={point.shortDesc.metaComment || ""} placeholder="Note..." title="Note associée à cette entrée dans l'historique"/> as HTMLInputElement;
		if (reg.hasPerm("action.wsp.histo#change.flag")) {
			point.startBtn.onclick = point.updateMetaFlag.bind(point);
		} else {
			point.startBtn.disabled = true;
		}
		if (reg.hasPerm("action.wsp.histo#change.comment")) {
			point.commentInput.onblur = point.updateMetaComment.bind(point);
		} else {
			point.commentInput.disabled = true;
		}
		return <div>
			<ShadowJsx skin="wed-histo-point/tooltip" skinOver="form-control-areas">
				{actionsBar}
				<div id="metas">
					{point.startBtn}
					{point.commentInput}
				</div>
				<div id="fields">
					<div class="field"><span class="label">Entrée du :</span><span class="value">{`${date} à ${time}`}</span></div>
					<div class="field"><span class="label">Modifié par :</span><span class="value"><UserRef î={{nickOrAccount: point.shortDesc.srcUser, reg} as OUserRefInit}/></span></div>
				</div>
			</ShadowJsx>
		</div>;
		// }
		// return null;
	}

	async updateMetaFlag() {
		try {
			await WSP.srcUpdateFields(this.histoPanel.reg.env.wsp, this, SRC.srcRef(this.shortDesc), {metaFlag: !this.shortDesc.metaFlag ? 1 : 0});
		} catch (e) {
			await ERROR.report("La modification de la marque de cette entrée a échoué.", e)
		}
		return this.histoPanel.updateHisto();
	}

	async updateMetaComment() {
		const v = this.commentInput.value;
		const metas: JSrcFields = {metaComment: v};
		if (!this.shortDesc.metaFlag && v && !this.shortDesc.metaComment) metas.metaFlag = 1; //Si meta nouvellement renseigné, on flag aussi
		try {
			await WSP.srcUpdateFields(this.histoPanel.reg.env.wsp, this, SRC.srcRef(this.shortDesc), metas);
		} catch (e) {
			await ERROR.report("L'enregistrement de la note pour cette entrée a échoué.", e)
		}
		return this.histoPanel.updateHisto();
	}
}

