export class OtherItemDiffPanel extends DiffPanelBase<IItemUiEnv> {

	action: OtherItemDiffAction;

	diffMode: EXmlDiffMode;
	diffModeBtn: ButtonActions<OtherItemDiffPanel>;
	srcDiff: SrcPointer;

	initPanel(action: OtherItemDiffAction, bar: IWedDiffBar): this {
		this._initPanel(action, bar);
		this.diffMode = EXmlDiffMode.diff;
		this.srcDiff = new SrcPointer().initialize({
			reg: this.reg as IReg<IWspUiEnv>,
			defaultAction: SrcPointerFastSelect.SINGLETON,
			actionsLists: ["actions:wsp:wed:otherItemDiff", "actions:wsp:shortDesc"],
			accelKeysLists: ["accelkeys:wsp:wed:otherItemDiff", "accelkeys:wsp:shortDesc"],
			draggable: true,
			dropable: true
		});
		this.srcDiff.onSrcRefChange.add(this.onSrcDiffChange);
		this.diffModeBtn = new ButtonActions<OtherItemDiffPanel>().initialize({
			reg: this.reg,
			label: diffModeActions[0].getLabel(this),
			actions: diffModeActions,
			actionContext: this
		});
		this.shadowRoot.append(
			this.srcDiff,
			this.result,
			this.diffModeBtn
		);
		return this;
	}

	setDiff(params: JOtherItemWedDiffBarParams | null): void {
		this.srcDiff.setSrcRef(params?.srcDiff);
		if (params) this.diffMode = params.mode || EXmlDiffMode.diff;
		diffModeActions[this.diffMode === EXmlDiffMode.diff ? 0 : 1].execute(this);
	}

	buildDiffParams(): JOtherItemWedDiffBarParams {
		return {diffId: this.action.getId(), srcDiff: this.srcDiff.srcRef, mode: this.diffMode}
	}

	async execDiff(): Promise<void> {
		const sd = this.srcDiff.shortDesc;
		if (sd) {
			const itemType = this.reg.env.wsp.wspMetaUi?.getItemType(sd.itModel);
			if (itemType) return callDiff(itemType.getMainXmlSrcUri(sd), this, this.diffMode);
		}
		return callDiff(null, this, this.diffMode);
	}

	protected onSrcDiffChange(this: void, drawer: SrcPointer) {
		DOMSH.findHost<OtherItemDiffPanel>(drawer).execDiff();
	}
}

function execDiff(this: Action<OtherItemDiffPanel>, ctx: OtherItemDiffPanel) {
	ctx.diffMode = this.getId() === "diff" ? EXmlDiffMode.diff : EXmlDiffMode.patch;
	ctx.diffModeBtn.label = this.getLabel(ctx);
	if (!ctx.srcDiff.fetchPending) ctx.execDiff();
}

const diffModeActions = [
	new Action<OtherItemDiffPanel>("diff").setLabel("Mode écarts").setExecute(execDiff),
	new Action<OtherItemDiffPanel>("patch").setLabel("Mode contributions").setExecute(execDiff)
];


