/**
 *
 * @param srcRef
 *  si "" pas de source de diff, mais un simple refresh pourait en proposer un
 *  si null pas de source de diff et aucune actuellement disponible (action user de sélection nécessaire).
 */
export async function callDiff(srcRef: srcRef | "" | null, panel: DiffPanelBase<IItemUiEnv>, mode: EXmlDiffMode) {
	try {
		if (srcRef) {
			panel.updateDiffResult("computing");
			const dom = await fetchDiff(panel.wedMgr, panel.reg.env.wsp.code, srcRef);
			//console.log(":::execDiff:::", dom);
			if (dom) {
				const session = new XmlDiffSession(dom, mode, panel.wedMgr.docHolder.getSubRoot());
				panel.wedMgr.docHolder.setDiffSession(session);
				panel.updateDiffResult(session.diffFound);
			} else {
				panel.wedMgr.docHolder.setDiffSession(null);
				panel.updateDiffResult("error");
			}
		} else {
			panel.wedMgr.docHolder.setDiffSession(null);
			panel.updateDiffResult(srcRef === "" ? "computable" : null);
		}
	} catch (e) {
		ERROR.log("Diff failed", e);
		panel.wedMgr.docHolder.setDiffSession(null);
		panel.updateDiffResult("error");
	}
}

async function fetchDiff(wedMgr: WedMgr, candidateWspCd: wspCd, candidateRef: srcRef | ESpecialCandidateUri, tries?: number): Promise<Document> {
	const reg = wedMgr.reg;
	const wsp = reg.env.wsp;
	const srcFields = (wedMgr.docHolder.house as IWspXmlHouse).srcFields;
	const srcRef = ITEM.isHistoryOrTrashUri(srcFields.srcUri) ? srcFields.srcUri : SRC.srcRef(srcFields);
	const wspRef = WSP.buildWspRef(wsp.code, srcRef);
	if (reg.env.place.isHouseDirty(wspRef)) await reg.env.place.wspsLive.saveHouse(wspRef);
	const doc = await WSP.fetchDiff(reg.env.wsp, reg.env.uiRoot, srcRef, candidateWspCd, candidateRef);
	const sd = reg.env.place.wspsLive.getHouseIfFetched(wspRef)?.srcFields;
	if (sd && (sd.srcStamp ? sd.srcStamp !== doc.documentElement.getAttribute("srcStamp") : sd.srcDt !== Number.parseInt(doc.documentElement.getAttribute("srcDt")))) {
		//La base comparée n'est pas en phase avec notre item actuel.
		if (tries === 2) {
			//toujours pas bon après un 1er refetch, on tente un reload complet de la house.
			reg.env.place.wspsLive.reloadHouse(wspRef);
			return null; //l'ihm est reconstruit le diff sera réappelé après redraw.
		}
		// if (tries > 3) { JAMAIS appelé car quand tries === 2, car refresh ui complet.
		// 	ERROR.log("fetchDiff: item out of sync after 3 tries");
		// 	POPUP.showNotifError("Impossible d'effectuer la comparaison", reg.env.uiRoot);
		// 	return null;
		// }
		return fetchDiff(wedMgr, candidateWspCd, candidateRef, (tries || 0) + 1);
	}
	return doc;
}
